import { apiServices } from "constants/apiConstants";

export const changePassword = {
  method: "post",
  url: `${apiServices}/users/setPassword`,
  errorMessages: {
    500: { message: "   ", type: "danger" },
    505: { message: "   ", type: "danger" },
    200: { message: "  ", type: "success" },
  },
};

export const setUserName = {
  method: "post",
  url: `${apiServices}/users/setUsername`,
  errorMessages: {
    500: { message: "   ", type: "danger" },
    200: { message: "   ", type: "success" },
  },
};

export const setUserPhoto = {
  method: "post",
  url: `${apiServices}/users/setPhoto`,
  body: { data: "photo" },
  errorMessages: {
    500: { message: "   ", type: "danger" },
    200: { message: "   ", type: "success" },
  },
};
